@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
:root {
  --background-color: #ddd;
  --primary-color: #e00;
  --container-color: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  background-color: var(--background-color);
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 1.1rem;
  line-height: 1.6;
  display: flex;
}

.header {
  background-color: var(--primary-color);
  color: #fff;
  padding: 10px;
  position: relative;
}

.btn-menu {
  color: var(--container-color);
  background-color: #0000;
  border: none;
  font-size: 2rem;
  position: absolute;
  top: 40px;
  left: 20px;
}

.btn-menu:hover {
  background-color: var(--primary-color);
  cursor: pointer;
}

h1 {
  text-align: center;
  margin: 10px 0;
  padding: 10px;
}

h1 a {
  color: #fff;
  text-decoration: none;
}

p {
  padding: 20px 0;
}

.navbar {
  padding: 10px;
  display: none;
}

.navbar-list {
  flex-direction: column;
  padding: 10px;
  list-style: none;
  display: flex;
}

.navbar-item {
  padding: 5px;
  font-weight: 600;
}

.navbar-link {
  color: #fff;
  font-size: 1.5rem;
  text-decoration: none;
}

.navbar-link:hover {
  color: var(--container-color);
  text-decoration: underline;
}

.show_menu {
  display: block;
}

main {
  flex: 1;
}

.container {
  width: 90%;
  margin-inline: auto;
  padding: 10px;
}

@media (width >= 800px) {
  .container {
    flex-wrap: wrap;
    gap: 25px;
    width: 70%;
    margin-inline: auto;
    padding: 10px;
    display: flex;
  }
}

h2 {
  padding: 10px 0;
}

h3 {
  text-align: center;
  text-transform: uppercase;
}

img {
  border-radius: 5px;
  max-width: 100%;
  margin: 20px 0;
  margin-inline: auto;
  display: block;
  box-shadow: 2px 4px 10px #0006;
}

figcaption {
  text-align: center;
  font-size: .8rem;
}

.categoria-link {
  margin-inline: center;
}

article {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  margin: 20px 0;
  padding: 20px 30px;
  display: flex;
  box-shadow: 2px 10px 10px #666;
}

@media (width >= 1200px) {
  article {
    flex-basis: 500px;
  }
}

.show {
  display: block;
  transform: translateY(0);
}

.list-detalles {
  justify-content: space-evenly;
  margin: 10px;
  padding: 10px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.list-detalles li a {
  color: #000;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 5px 20px;
  font-weight: 600;
  text-decoration: none;
}

.list-detalles li a:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

section {
  border-bottom: 1px solid #000;
  margin: 25px;
  padding: 25px 10px;
}

.read-more {
  background-color: var(--primary-color);
  color: var(--container-color);
  text-align: right;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: 700;
  text-decoration: none;
}

.read-more:hover {
  background-color: var(--container-color);
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

.detalles-list {
  text-align: center;
  flex-wrap: wrap;
  gap: 15px;
  padding: 20px 0;
  display: flex;
}

.detalles-links {
  flex-grow: 1;
  flex-basis: 200px;
  list-style: none;
}

.detalles-links a {
  background-color: var(--primary-color);
  color: var(--container-color);
  border-radius: 5px;
  padding: 5px 15px;
  font-weight: 700;
  text-decoration: none;
}

.detalles-links a:hover {
  background-color: var(--container-color);
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

footer {
  color: #fff;
  text-align: center;
  background-color: #e00;
  padding: 10px;
  font-size: 1.5rem;
  font-weight: 600;
}

.social-media__list {
  justify-content: center;
  align-items: center;
  gap: 12px;
  list-style: none;
  display: flex;
}

.social-media__link {
  color: #fff;
  padding: 10px;
  font-size: 2.5rem;
}
