@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

    :root {
      --background-color: #ddd;
      --primary-color: #ee0000;
      --container-color: #fff;
    }

    * {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }

    body, html {
      height: 100%;
      margin: 0;
      display: flex;
      flex-direction: column;
      font-size: 1.1rem;
      line-height: 1.6;
      font-family: "Poppins", sans-serif;
      background-color: var(--background-color);
    }

    .header {
      background-color: var(--primary-color);
      color: #fff;
      padding: 10px;
      position: relative;
    }

    .btn-menu {
      position: absolute;
      top: 40px;
      left: 20px;
      font-size: 2rem;
      background-color: transparent;
      border: none;
      color: var(--container-color);
    }

    .btn-menu:hover {
      background-color: var(--primary-color);
      cursor: pointer;
    }

    h1 {
      text-align: center;
      padding: 10px;
      margin: 10px 0;
    }

    h1 a {
      color: #fff;
      text-decoration: none;
    }

    p {
      padding: 20px 0;
    }

    .navbar {
      padding: 10px;
      display: none;
    }

    .navbar-list {
      display: flex;
      flex-direction: column;
      padding: 10px;
      list-style: none;
    }

    .navbar-item {
      padding: 5px;
      font-weight: 600;
    }

    .navbar-link {
      font-size: 1.5rem;
      color: #fff;
      text-decoration: none;
    }

    .navbar-link:hover {
      color: var(--container-color);
      text-decoration: underline;
    }

    .show_menu {
      display: block;
    }


main {
  flex: 1
}

.container {
  width: 90%;
  padding: 10px;
  margin-inline: auto;
}

@media (min-width: 800px) {
  .container {
    width: 70%;
    padding: 10px;
    margin-inline: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
  }
}

h2 {
  padding: 10px 0px;
}

h3 {
  text-align: center;
  text-transform: uppercase;
}

img {
  display: block;
  max-width: 100%;
  margin: 20px 0;
  margin-inline: auto;
  border-radius: 5px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.4)
}

figcaption {
  text-align: center;
  font-size: .8rem;
}

.categoria-link {
  margin-inline: center;
}

article {
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  margin: 20px 0px;
  border-radius: 5px;
  box-shadow: 2px 10px 10px #666;
  background-color: #fff;
}

@media (min-width: 800px) {
  article {
  }
}

@media (min-width: 1200px) {
  article {
    flex-basis: 500px;
  }
}

.show {
  display: block;
  transform: translateY(0px);
}

.list-detalles {
  display: flex;
  justify-content: space-evenly;
  text-decoration: none;
  padding: 10px;
  margin: 10px;
  list-style: none;
}

.list-detalles li a {
  text-decoration: none;
  font-weight: 600;
  padding: 5px 20px;
  border: 1px solid #000;
  border-radius: 5px;
  color: #000;
}

.list-detalles li a:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

section {
  padding: 25px 10px;
  margin: 25px;
  border-bottom: 1px solid #000;
}

.read-more {
  padding: 5px 10px;
  font-weight: bold;
  text-align: right;
  text-decoration: none;
  background-color: var(--primary-color);
  color: var(--container-color);
  border-radius: 5px;
}

.read-more:hover {
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  background-color: var(--container-color);
}

.detalles-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  text-align: center;
  padding: 20px 0;
}

.detalles-links {
  flex-grow: 1;
  flex-basis: 200px;
  list-style: none;
}

.detalles-links a {
  text-decoration: none;
  background-color: var(--primary-color);
  color: var(--container-color);
  border-radius: 5px;
  padding: 5px 15px;
  font-weight: bold;
}

.detalles-links a:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background-color: var(--container-color);
}

footer {
  background-color: #ee0000;
  text-align: center;
  padding: 10px;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
}

.social-media__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  list-style: none;
}

.social-media__link {
  font-size: 2.5rem;
  color: #fff;
  padding: 10px;

}
